import { useLocation } from 'react-router-dom'
import loadingGif from '../shared/images/pokeball.gif'
import { useFetch } from '../hooks/useFetch'

export const ItemDetail = () => {
	const location = useLocation<any>()
	const { data, loading, error } = useFetch(location.state.url)

	const displayData = () => {
		if (!loading) {
			return (
				<>
					<img src={data?.sprites?.front_default} alt={'error'} />
					<h1>name: {data?.name}</h1>
					<h1>hair_color: {data?.hair_color}</h1>
					<h1>gender: {data?.gender}</h1>
					<h1>height: {data?.height} cm</h1>
				</>
			)
		}
	}

	return (
		loading ? <img src={loadingGif} alt={'errorrrr'} /> :
			<div>
				{displayData()}
				<button onClick={() => console.log(data)}>show useFetch</button>
			</div>
	)
}
